import React from 'react'
import {SiDropbox} from 'react-icons/si'
import {GiDiploma} from 'react-icons/gi'

export const ProductDevolution = () => {
    return (
        <div className = "animate__animated animate__fadeIn">
            <div className ="flex justify-between gap-7 items-center">
                <div className = "rounded-full p-4 bg-gray-200">
                    <SiDropbox className = "text-6xl text-black"></SiDropbox>
                </div>
                <div>
                    <h3 className = "text-xl font-semibold mb-2">Cambios y Devoluciones</h3>
                    <p>Puedes acercarte a cualquier tienda ElectroStore a realizar el cambio o devolucin del producto, de manera gratuita, tienes desde 7 hasta 30 das hbiles dependiendo del tipo de producto. Para mayor informacin, revisa nuestras polticas sobre cambios y devoluciones, comuncate al (073) 301402 o escrbenos por whatsapp (959686193)</p>
                </div>
            </div>
            <div className ="flex justify-between gap-7 items-center mt-8">
                <div className = "rounded-full p-4 bg-gray-200">
                    <GiDiploma className = "text-6xl text-black"></GiDiploma>
                </div>
                <div>
                    <h3 className = "text-xl font-semibold mb-2">Garanta y Asistencia</h3>
                    <p>Los productos que Ripley comercializa cuentan con garanta de funcionamiento cuyo periodo de vigencia vara dependiendo del tipo de producto. Para mueblera y electro, te recomendamos revisar el certificado de garanta que llega con el producto.</p>
                </div>
            </div>
        </div>
    )
}
